.CartOverlay__containerWithCartImg {
    width: 50%;
    display: flex;
    align-items: center;
    padding-right: 15px;
    justify-content: center;
    cursor: pointer;
    transition: 0.15s linear;
    position: relative;
}

.CartOverlay__containerWithCartImg:hover {
    background: #EEEEEE;
}

.CartOverlay__containerWithCartImgActive{
    background: #EEEEEE;
    box-shadow: 0 0  10px rgba(0, 0, 0, 0.05);
}

.CartOverlay__containerWithCartImgImg {
    height: 18px;
    width: 20px;
}

.CartOverlay__amountImg {
    border-radius: 50%;
    background-color: black;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    position: absolute;
    right: 15px;
    top: 0;
}

.CartOverlay__container {
    position: absolute;
    top: calc(100% + 20px);
    right: 0;
    width: 372px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: scaleY(1);
    transform-origin: 100% 0;
    transition: 0.1s linear;
    z-index: 10;
    background: white;
    cursor: auto;
}
::-webkit-scrollbar { width: 3px; height: 3px;}
::-webkit-scrollbar-button {  background-color: #666; }
::-webkit-scrollbar-track {  background-color: #999;}
::-webkit-scrollbar-track-piece { background-color: #ffffff;}
::-webkit-scrollbar-thumb { height: 50px; background-color: #666; border-radius: 3px;}
::-webkit-scrollbar-corner { background-color: #999;}
::-webkit-resizer { background-color: #666;}





.CartOverlay__containerHide {
    transform: scaleY(0);
}


